"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

interface JournalEntry {
  id: string;
  date: string;
  description: string;
  reference: string;
  entries: Array<{
    account: string;
    debit: number;
    credit: number;
  }>;
  status: 'draft' | 'posted' | 'pending';
  source: 'manual' | 'ai-generated' | 'imported';
}

export default function JournalPage() {
  const [journalEntries, setJournalEntries] = useState<JournalEntry[]>([]);
  const [selectedEntry, setSelectedEntry] = useState<JournalEntry | null>(null);
  const [isGenerating, setIsGenerating] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Load existing journal entries
    loadJournalEntries();
  }, []);

  const loadJournalEntries = async () => {
    try {
      const response = await fetch('/api/journal');
      if (response.ok) {
        const data = await response.json();
        setJournalEntries(data);
      }
    } catch (err) {
      console.error('Failed to load journal entries:', err);
    }
  };

  const generateSmartEntry = async () => {
    setIsGenerating(true);
    setError(null);

    try {
      const response = await fetch('/api/journal/generate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          type: 'smart_suggestion',
          context: 'general_business_transaction'
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to generate journal entry');
      }

      const newEntry = await response.json();
      setJournalEntries(prev => [newEntry, ...prev]);
      setSelectedEntry(newEntry);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to generate journal entry');
    } finally {
      setIsGenerating(false);
    }
  };

  const postEntry = async (entryId: string) => {
    try {
      const response = await fetch(`/api/journal/${entryId}/post`, {
        method: 'POST',
      });

      if (!response.ok) {
        throw new Error('Failed to post journal entry');
      }

      // Update the entry status
      setJournalEntries(prev =>
        prev.map(entry =>
          entry.id === entryId
            ? { ...entry, status: 'posted' as const }
            : entry
        )
      );
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to post journal entry');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'posted': return 'bg-green-100 text-green-800';
      case 'pending': return 'bg-yellow-100 text-yellow-800';
      case 'draft': return 'bg-gray-100 text-gray-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getSourceColor = (source: string) => {
    switch (source) {
      case 'ai-generated': return 'bg-blue-100 text-blue-800';
      case 'imported': return 'bg-purple-100 text-purple-800';
      case 'manual': return 'bg-gray-100 text-gray-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Journal Entries</h1>
          <p className="text-muted-foreground">Smart automated journal entries with AI assistance</p>
        </div>
        <Button onClick={generateSmartEntry} disabled={isGenerating}>
          {isGenerating ? 'Generating...' : 'Generate Smart Entry'}
        </Button>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <div className="grid gap-6 md:grid-cols-3">
        {/* Journal Entries List */}
        <div className="md:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle>Recent Journal Entries</CardTitle>
              <CardDescription>
                View and manage your accounting journal entries
              </CardDescription>
            </CardHeader>
            <CardContent>
              {journalEntries.length > 0 ? (
                <div className="space-y-4">
                  {journalEntries.map((entry) => (
                    <div
                      key={entry.id}
                      className={`p-4 border rounded-lg cursor-pointer transition-colors hover:bg-muted/50 ${
                        selectedEntry?.id === entry.id ? 'border-primary bg-primary/5' : ''
                      }`}
                      onClick={() => setSelectedEntry(entry)}
                    >
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center gap-2">
                          <span className="font-medium">{entry.reference}</span>
                          <Badge className={getStatusColor(entry.status)}>
                            {entry.status}
                          </Badge>
                          <Badge variant="outline" className={getSourceColor(entry.source)}>
                            {entry.source}
                          </Badge>
                        </div>
                        <span className="text-sm text-muted-foreground">{entry.date}</span>
                      </div>
                      <p className="text-sm text-muted-foreground mb-2">{entry.description}</p>
                      <div className="flex justify-between text-sm">
                        <span>
                          Debits: ${entry.entries.reduce((sum, e) => sum + e.debit, 0).toFixed(2)}
                        </span>
                        <span>
                          Credits: ${entry.entries.reduce((sum, e) => sum + e.credit, 0).toFixed(2)}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8">
                  <img 
                    src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/f50e033a-de0f-40c3-8a8e-a54e72100d67.png" 
                    alt="Smart journal entries management interface"
                    className="mx-auto mb-4 opacity-50 rounded"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.style.display = 'none';
                    }}
                  />
                  <p className="text-muted-foreground mb-4">
                    No journal entries yet. Generate your first smart entry!
                  </p>
                  <Button onClick={generateSmartEntry} disabled={isGenerating}>
                    {isGenerating ? 'Generating...' : 'Create First Entry'}
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Entry Details */}
        <div>
          <Card>
            <CardHeader>
              <CardTitle>Entry Details</CardTitle>
              <CardDescription>
                View and manage selected journal entry
              </CardDescription>
            </CardHeader>
            <CardContent>
              {selectedEntry ? (
                <div className="space-y-4">
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">Reference</label>
                    <p className="font-medium">{selectedEntry.reference}</p>
                  </div>
                  
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">Date</label>
                    <p className="font-medium">{selectedEntry.date}</p>
                  </div>
                  
                  <div>
                    <label className="text-sm font-medium text-muted-foreground">Description</label>
                    <p className="font-medium">{selectedEntry.description}</p>
                  </div>

                  <div className="flex gap-2">
                    <Badge className={getStatusColor(selectedEntry.status)}>
                      {selectedEntry.status}
                    </Badge>
                    <Badge variant="outline" className={getSourceColor(selectedEntry.source)}>
                      {selectedEntry.source}
                    </Badge>
                  </div>

                  <Separator />

                  <div>
                    <h4 className="font-medium mb-3">Account Entries</h4>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Account</TableHead>
                          <TableHead className="text-right">Debit</TableHead>
                          <TableHead className="text-right">Credit</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {selectedEntry.entries.map((entry, index) => (
                          <TableRow key={index}>
                            <TableCell className="font-medium">{entry.account}</TableCell>
                            <TableCell className="text-right">
                              {entry.debit > 0 ? `$${entry.debit.toFixed(2)}` : '-'}
                            </TableCell>
                            <TableCell className="text-right">
                              {entry.credit > 0 ? `$${entry.credit.toFixed(2)}` : '-'}
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>

                  <Separator />

                  <div className="space-y-2">
                    {selectedEntry.status === 'draft' && (
                      <Button 
                        onClick={() => postEntry(selectedEntry.id)}
                        className="w-full"
                      >
                        Post Entry
                      </Button>
                    )}
                    {selectedEntry.status === 'posted' && (
                      <div className="text-center py-2">
                        <Badge className="bg-green-100 text-green-800">
                          Entry Posted Successfully
                        </Badge>
                      </div>
                    )}
                  </div>
                </div>
              ) : (
                <div className="text-center py-8">
                  <img 
                    src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/cb225446-4821-41c7-b783-ad3fe301c97c.png" 
                    alt="Journal entry details view"
                    className="mx-auto mb-4 opacity-50 rounded"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.style.display = 'none';
                    }}
                  />
                  <p className="text-muted-foreground">
                    Select a journal entry to view details
                  </p>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
